<template>
  <div class="userSetting">

    <div class="menmuTitle flex align between">
      <h1>账号设置</h1>
    </div>
    <!-- menmuTitle -->

    <div class="space50"></div>

    <div class="setItem">
      <div class="ietmTitle">设置头像（建议尺寸100px*100px）</div>
      <div class="space20"></div>
      <UploadImg actionPath="/proxyUrl/wallet/v1/user/headUpload" @loadDone="actionUserInfo"/>
    </div>
    <!-- setItem -->


    <hr style="margin:30px 0;">


    <div class="setItem flex align between">
      <div class="ietmTitle flex align"><span>设置用户名&nbsp;&nbsp;（当前账户名“{{userInfo.user.userName}}”）</span></div>
      <el-button type="primary" @click="open">立即设置</el-button>
    </div>
    <!-- setItem -->


  </div>
</template>

<script>
  import {
    mapMutations,
    mapState,
    mapActions
  } from 'vuex'

  import UploadImg from '@com/common/uploadImg'

  import {
    changeUsername,headUpload
  } from '../data/apis';
  

  export default {
    data() {
      return {
        imageUrl: ''
      }
    },
    computed: {
      ...mapState(['userInfo'])
    },
    components:{
      UploadImg
    },
    methods: {
      ...mapActions(['actionUserInfo']),
      open() {
        this.$prompt('请输入账户新名', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
        }).then(({ value }) => {
          if(!value){
              return this.$message.error('请输入新用户名');
          }
          changeUsername({userName:value}).then(v=>{
            this.$message.success(v.message);
            this.actionUserInfo();
          })
        })
      }//open
    },
    mounted() {

    } //mounted
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../styles/Val";
   
</style>



